/* Big tablet to 1200px: widths smaller than 1140px row */

@media only screen and (max-width: 1200px) {}


/* Small tablet to big tablets: from 768px to 1023px */

@media only screen and (max-width: 1023px) {
    .name-container {
        padding: 15%;
    }
    .cBreak-container {
        padding-top: 5%;
    }
    .cBreak-container .cBreak {
        width: 100%;
    }
    #devfolio-apply-now {
        margin-top: 30%;
    }
    #about {
        margin-top: 60px;
    }
    .sponsor-message {
        margin-top: 60px;
        margin-bottom: 60px;
    }
    #prize {
        margin-top: 80px !important;
        margin-bottom: 20px;
    }
    .slick-track {
        padding-top: 15px;
        /* margin-top: 40px; */
        margin-bottom: 40px;
    }
    .faq-boxes {
        margin-top: 60px;
    }
    #timelineDiv {
        margin-top: 80px !important;
    }
    .timeline {
        margin-top: 60px;
        padding-bottom: 60px;
    }
    #contact {
        margin-top: 10px;
    }
}


/* Small phones to Small tablets: from 481px to 767px */

@media only screen and (max-width: 767px) {
    .top-animation {
        margin-top: 55px !important;
    }
    .cBreak-container {
        width: 50% !important;
    }
    #devfolio-apply-now {
        margin-top: 10% !important;
    }
    #about {
        margin-top: 60px;
    }
    .sponsor-message {
        margin-top: 60px;
        margin-bottom: 60px;
    }
    #prize {
        margin-top: 60px !important;
    }
    .slick-track {
        padding-top: 15px;
        /* margin-top: 40px; */
        margin-bottom: 40px;
    }
    .faq-boxes {
        margin-top: 60px;
    }
    #timelineDiv {
        margin-top: 80px !important;
    }
    .timeline {
        margin-top: 60px;
        padding-bottom: 60px;
    }
    #contact {
        margin-top: 10px;
    }
    .social-media-buttons {
        margin-left: 4% !important;
        margin-top: 30px !important;
    }
    .map {
        margin-top: 40px;
        width: 70% !important;
    }
    .card {
        margin-top: 100px;
        margin-right: 30px !important;
        width: 65% !important;
    }
    #typewriter {
        margin-top: 33%;
    }
    .card-content h1 {
        padding-right: 10% !important;
        font-size: 110% !important;
    }
    .menu-bar {
        position: fixed;
        z-index: 1;
        transform: translateX(-20%);
        top: 85%;
    }
}


/* Small phones: from 0 to 480px */

@media only screen and (max-width: 480px) {
    .sponsor-t {
        margin-top: 15%;
    }
    .menu-bar {
        position: fixed;
        z-index: 1;
        top: 85%;
    }
    .top-animation {
        margin-top: 10px;
    }
    .left-animation {
        display: none !important;
    }
    .cBreak-container {
        padding-top: 0;
        width: 90% !important;
    }
    #devfolio-apply-now {
        margin-top: 25% !important;
    }
    .hourglass1 {
        display: none !important;
    }
    .menu-bar {
        position: fixed;
        z-index: 1;
        transform: translateX(-30%);
        top: 85%;
    }
    .card {
        margin-top: 33%;
        width: 79% !important;
    }
    #typewriter {
        margin-top: 33%;
    }
}


